<template>
  <div class="login-container">
    <el-form autoComplete="on" :model="loginForm" :rules="loginRules" ref="loginForm" label-position="left" label-width="0px"
      class="card-box login-form">
      <h3 class="title">磁易购论坛后台系统登录</h3>
      <el-form-item prop="email">
        <span class="svg-container">
                  <img src="../../assets/images/user-icon.svg"/>
                </span>
        <el-input name="mobile" type="text" v-model="loginForm.mobile" autoComplete="on" placeholder="用户名"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <span class="svg-container">
                  <icon-svg icon-class="mima" ></icon-svg>
                </span>
        <el-input name="password" type="password" @keyup.enter.native="handleLogin" v-model="loginForm.password" autoComplete="on"
          placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleLogin">
          登录
        </el-button>
      </el-form-item>
     <!-- <div class='tips'>admin账号为:admin@wallstreetcn.com 密码随便填</div>
      <div class='tips'>editor账号:editor@wallstreetcn.com 密码随便填</div>-->
    </el-form>
  </div>
</template>

<script>
    import { validatePhoneNumber } from '@/utils/validate';
    import { Message } from 'element-ui';
    export default {
      name: 'login',
      data() {
        const validatePhone = (rule, value, callback) => {
          if (!validatePhoneNumber(value)) {
            callback(new Error('请输入合法的手机号'));
          } else {
            callback();
          }
        };
        const validatePass = (rule, value, callback) => {
          if (value.length < 6) {
            callback(new Error('密码不能小于6位'));
          } else {
            callback();
          }
        };
        return {
          loginForm: {
            mobile: '',
            password: '',
            role_name:2
          },
          //进行手机号和密码的验证
          loginRules: {
              mobile: [
                { required: true, trigger: 'blur', validator: validatePhone }
            ],
            password: [
                { required: true, trigger: 'blur', validator: validatePass }
            ]
          },
          loading: false
        }
      },
      methods: {
        /**
         * 处理登录的逻辑
        */
        handleLogin() {
          const _this = this;
          this.$refs.loginForm.validate(valid => {
            if (valid) {
                  this.loading = true;
                  this.$store.dispatch('Login', this.loginForm).then(() => {
                      this.loading = false;
                      //登录完成跳转到首页
                      this.$router.push({ path: '/' });
                  }).catch((err) => {
                      if(parseInt(err.response.data.errorcode[0])===30000){
                          this.showMsg("电话号码错误!");
                      }
                      if(parseInt(err.response.data.errorcode[0])===30001){
                          this.showMsg("密码错误!");
                      }
                      this.loading = false;
                  });
            } else {
              return false;
            }
          });
        },
        /**
        * 提示信息
        */
        showMsg(message){
            Message({
                message: message,
                type: 'error',
                duration: 5 * 1000
            });
        }
      }
    }
</script>

<style rel="stylesheet/scss" lang="scss">
    @import "src/styles/mixin.scss";
    .tips {
      font-size: 14px;
      color: #fff;
      margin-bottom: 5px;
    }

    .login-container {
      @include relative;
      height: 100vh;
      background-color: #2d3a4b;
      input:-webkit-autofill {
        -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
        -webkit-text-fill-color: #fff !important;
      }
      input {
        background: transparent;
        border: 0px;
        -webkit-appearance: none;
        border-radius: 0px;
        padding: 12px 5px 12px 15px;
        color: #eeeeee;
        height: 47px;
      }
      .el-input {
        display: inline-block;
        height: 47px;
        width: 85%;
      }
      .svg-container {
        padding: 6px 5px 6px 15px;
        color: #889aa4;
      }
      .title {
        font-size: 26px;
        font-weight: 400;
        color: #eeeeee;
        margin: 0px auto 40px auto;
        text-align: center;
        font-weight: bold;
      }
      .login-form {
        position: absolute;
        left: 0;
        right: 0;
        width: 400px;
        padding: 35px 35px 15px 35px;
        margin: 120px auto;
      }
      .el-form-item {
        border: 1px solid rgba(255, 255, 255, 0.1);
        background: rgba(0, 0, 0, 0.1);
        border-radius: 5px;
        color: #454545;
      }
      .forget-pwd {
        color: #fff;
      }
    }
</style>



